import { constants } from '../constants';
//import { PlayScene } from "./PlayScene";
import menuBackgroundImage from '../assets/title_bg.jpg';
import menuTitleImage from '../assets/logo.png';
import playButtonImage from '../assets/play_button.png';
import optionButtonImage from '../assets/options_button.png'

export class MenuScene extends Phaser.Scene {

  constructor() {
    super({
      key: constants.SCENES.MENU
    })
  }

  preload() {
    this.load.image('menuTitle', menuTitleImage);
    this.load.image('menuBackground', menuBackgroundImage);
    this.load.image('playButton', playButtonImage);
    this.load.image('optionsButton', optionButtonImage);
  }

  create() {
    //this.scene.add(constants.SCENES.PLAY, PlayScene, false)
    this.add.image(0, 0, 'menuBackground').setOrigin(0).setDepth(0);
    this.add.image(this.game.renderer.width / 2, this.game.renderer.height * 0.2, 'menuTitle').setDepth(1);
    let playButton = this.add.image(this.game.renderer.width / 2, this.game.renderer.height / 2, 'playButton').setDepth(1);
    this.add.image(this.game.renderer.width / 2, this.game.renderer.height / 2 + 100, 'optionsButton').setDepth(1);

    let hoverIndicator = this.add.circle(0, 0, 15, 0xD10003);
    hoverIndicator.setVisible(false);

    //this.scene.start(constants.SCENES.PLAY, "hello");
    playButton.setInteractive();

    playButton.on('pointerover', ()=> {
      hoverIndicator.x = playButton.x - (playButton.width / 2);
      hoverIndicator.y = playButton.y;
      hoverIndicator.setVisible(true);
    })

    playButton.on('pointerout', ()=> {
      hoverIndicator.setVisible(false);
    })

    playButton.on('pointerup', ()=> {
      console.log('pointerup!')
    })
  }
}
